"use client";

import React, { useMemo, useState } from "react";
import Image from "next/image";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Modal } from "@/components/ui/modal";
import { Eye, Plus } from "lucide-react";

/* ---------------- TYPES ---------------- */
type Status = "Draft" | "Submitted" | "Approved" | "Rejected";

interface TravelExpense {
    id: number;
    title: string;
    travelType: string;
    from: string;
    to: string;
    date: string;
    amount: number;
    status: Status;
    receipts: string[];
}

/* ---------------- MOCK DATA ---------------- */
const initialData: TravelExpense[] = [
    {
        id: 1,
        title: "Client Visit - Bangalore",
        travelType: "Flight",
        from: "Chennai",
        to: "Bangalore",
        date: "2025-01-05",
        amount: 8500,
        status: "Submitted",
        receipts: ["/images/sample-receipt.png"],
    },
    {
        id: 2,
        title: "Office Commute",
        travelType: "Cab",
        from: "Home",
        to: "Office",
        date: "2025-01-10",
        amount: 1200,
        status: "Draft",
        receipts: [],
    },
];

/* ---------------- COMPONENT ---------------- */
export default function TravelExpensesPage() {
    const [data, setData] = useState<TravelExpense[]>(initialData);
    const [openForm, setOpenForm] = useState(false);
    const [openView, setOpenView] = useState<TravelExpense | null>(null);

    const [statusFilter, setStatusFilter] = useState("");
    const [monthFilter, setMonthFilter] = useState("");

    /* ---------------- FILTER ---------------- */
    const filtered = useMemo(() => {
        return data.filter((r) => {
            const matchStatus = statusFilter ? r.status === statusFilter : true;
            const matchMonth = monthFilter
                ? r.date.startsWith(monthFilter)
                : true;
            return matchStatus && matchMonth;
        });
    }, [data, statusFilter, monthFilter]);

    /* ---------------- STATUS COLOR ---------------- */
    const statusColor = (status: Status) =>
        status === "Approved"
            ? "success"
            : status === "Submitted"
                ? "warning"
                : status === "Rejected"
                    ? "error"
                    : "secondary";

    return (
        <div className="p-6 space-y-6">
            {/* HEADER */}
            <div className="flex justify-between items-center">
                <h1 className="text-xl font-semibold">Travel Expenses</h1>
                <button
                    onClick={() => setOpenForm(true)}
                    className="flex items-center gap-2 px-4 py-2 rounded bg-brand-600 text-white hover:bg-brand-700"
                >
                    <Plus size={16} /> New Travel Expense
                </button>
            </div>

            {/* FILTERS */}
            <div className="flex gap-4">
                <select
                    className="border rounded px-3 py-2"
                    value={statusFilter}
                    onChange={(e) => setStatusFilter(e.target.value)}
                >
                    <option value="">All Status</option>
                    <option>Draft</option>
                    <option>Submitted</option>
                    <option>Approved</option>
                    <option>Rejected</option>
                </select>

                <input
                    type="month"
                    className="border rounded px-3 py-2"
                    value={monthFilter}
                    onChange={(e) => setMonthFilter(e.target.value)}
                />
            </div>

            {/* TABLE */}
            <div className="overflow-hidden rounded-xl border bg-white">
                <div className="overflow-x-auto min-w-[1100px]">
                    <Table>
                        <TableHeader className="border-b">
                            <TableRow>
                                {[
                                    "Title",
                                    "Travel Type",
                                    "Route",
                                    "Date",
                                    "Amount",
                                    "Status",
                                    "Action",
                                ].map((h) => (
                                    <TableCell
                                        key={h}
                                        isHeader
                                        className="px-5 py-3 text-left text-theme-xs font-medium text-gray-500"
                                    >
                                        {h}
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHeader>

                        <TableBody className="divide-y">
                            {filtered.map((r) => (
                                <TableRow key={r.id} className="hover:bg-gray-50">
                                    <TableCell className="px-5 py-4 font-medium">
                                        {r.title}
                                    </TableCell>

                                    <TableCell className="px-5 py-4">
                                        {r.travelType}
                                    </TableCell>

                                    <TableCell className="px-5 py-4">
                                        {r.from} → {r.to}
                                    </TableCell>

                                    <TableCell className="px-5 py-4">
                                        {r.date}
                                    </TableCell>

                                    <TableCell className="px-5 py-4">
                                        ₹{r.amount.toLocaleString()}
                                    </TableCell>

                                    <TableCell className="px-5 py-4">
                                        <Badge size="sm" color={statusColor(r.status)}>
                                            {r.status}
                                        </Badge>
                                    </TableCell>

                                    <TableCell className="px-5 py-4">
                                        <button
                                            onClick={() => setOpenView(r)}
                                            className="text-brand-600 hover:underline flex items-center gap-1"
                                        >
                                            <Eye size={14} /> View
                                        </button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </div>
            </div>

            {/* VIEW MODAL */}
            {openView && (
                <Modal
                    isOpen
                    onClose={() => setOpenView(null)}
                    className="max-w-xl p-6"
                >
                    <h2 className="text-lg font-semibold mb-4">
                        {openView.title}
                    </h2>

                    <div className="space-y-2 text-sm">
                        <p><b>Type:</b> {openView.travelType}</p>
                        <p><b>Route:</b> {openView.from} → {openView.to}</p>
                        <p><b>Date:</b> {openView.date}</p>
                        <p><b>Amount:</b> ₹{openView.amount}</p>
                    </div>

                    <div className="mt-4">
                        <h4 className="font-medium mb-2">Receipts</h4>
                        <div className="flex gap-3">
                            {openView.receipts.map((r, i) => (
                                <Image
                                    key={i}
                                    src={r}
                                    alt="receipt"
                                    width={80}
                                    height={80}
                                    className="rounded border"
                                />
                            ))}
                        </div>
                    </div>
                </Modal>
            )}

            {/* CREATE FORM */}
            {openForm && (
                <Modal
                    isOpen
                    onClose={() => setOpenForm(false)}
                    className="max-w-lg p-6"
                >
                    <h2 className="text-lg font-semibold mb-4">
                        New Travel Expense
                    </h2>

                    <form className="space-y-4">
                        <input className="w-full border rounded px-3 py-2" placeholder="Title" />
                        <select className="w-full border rounded px-3 py-2">
                            <option>Flight</option>
                            <option>Train</option>
                            <option>Cab</option>
                            <option>Hotel</option>
                            <option>Meal</option>
                        </select>
                        <div className="flex gap-2">
                            <input className="w-full border rounded px-3 py-2" placeholder="From" />
                            <input className="w-full border rounded px-3 py-2" placeholder="To" />
                        </div>
                        <input type="date" className="w-full border rounded px-3 py-2" />
                        <input type="number" className="w-full border rounded px-3 py-2" placeholder="Amount" />
                        <input type="file" multiple className="w-full" />

                        <div className="flex justify-end gap-2 pt-4">
                            <button
                                type="button"
                                className="px-4 py-2 border rounded"
                                onClick={() => setOpenForm(false)}
                            >
                                Cancel
                            </button>
                            <button
                                type="button"
                                className="px-4 py-2 bg-brand-600 text-white rounded"
                            >
                                Save as Draft
                            </button>
                        </div>
                    </form>
                </Modal>
            )}
        </div>
    );
}
